import Vue from "vue"
import vuex from "vuex"

Vue.use(vuex);

export default new vuex.Store({
	state:{
		QueryListData:[],//库存查询 库存查询详情页数据
		inventoryCondition:{},//库存查询条件
		// 判断是从库存查询页面跳转而来 还是从移动页面跳转而来
		RFInvMove:false,
		InventoryQuery:false,

		Receipt_ReceiptNum:"",//收货模块 收货单号
		Receipt_CustomerNum:"",//收货模块 客户单号
		Receipt_SelectOrderData:[],//收货模块  收货单号——选择订单页面数据
		Receipt_SubmitData:[],//收货模块 收货确认——前三个数据
		Receipt_SummaryData:[],//收货模块 收货汇总数据
		BindingCommodity_data:[],//收货 绑定商品条码选择页面数据
		StateOfGoodsData:[],//货物状态数组
		RFReceiveSkuData:[],//收货页面商品代码等数据 批属性字段是否显示数据
		Receipt_PrevBatchProp:[],//上一笔收货批属性值（显示并必填时显示上一笔批属性值）
		
		RFPick_SelectOrderData:[],//拣货模块 选择任务 数据
		RFPick_SubmitData:[],//拣货模块 拣货确认 数据

		IsConsignOrReceipt:"",//判断是 发货序列号采集 还是 收货序列号采集(默认收货)

		Consignment_SubmitData:[],//发货序列号采集  数据
		Consignment_ChoiceData:[],//发货序列号采集  选择商品数据
		Consignment_CheckDetailsData:[],//发货序列号采集  查看明细数据

		RFInvMoveCondition:{},//库存移动页面 查询条件

		CycleCount_RFCountSel:[],//盘点模块  选择订单  接口数据
		CycleCount_RFCountQry:[],//盘点模块  盘点详情  接口数据

		RFOrderShip_RFOrderQuery:{},//发运模块  发运查询 接口数据
		RFOrderShip_RFOrderDetailQuery:[],//发运模块  查看明细  接口数据

		WarehouseCode : "",  //仓库  调用接口所用  code
		WarehouseName:"", // 仓库 中文名称 
		UserID : "",  //用户名   调用接口所用
		Password:'',//密码
		limitControlList:[],//权限列表（首页模块权限显示与否）
		Tenant_Code:'',//租户代码

		// 正式
		Server_Address:'https://kdyrf.cy-scm.com:8056/rfInf/wms/',// 服务器地址
		Stationary_Server_Address : 'https://kdyrf.cy-scm.com:8056/rfInf/wms/', // 固定的 服务器地址

		// 测试
		// Server_Address:'https://nt.cy-scm.com/rfInf/wms/',// 服务器地址
		// Stationary_Server_Address : 'https://nt.cy-scm.com/rfInf/wms/', // 固定的 服务器地址

		ScanQRCodeInputName : "",  //扫码输入框名字

		ReplenishMode:"RN",//补货模式(默认为RN)
		Replenish_TaskData:[],//补货 任务列表数据
		Replenish_StatusArr:[],//补货 任务列表状态数组
		Replenish_area:'',//补货区域

		Bind_Openid:'',//微信登录 Openid

		App_Version:'1.0.0',//App版本号
	}
})


