<template>
  <div id="app" type="primary"  v-loading.fullscreen.lock="Loading"   element-loading-text="加载中">
    <router-view @isLoading="fullscreenLoading"/>
  </div>
</template>

<script>
import "../static/font/iconfont.css"
export default {
  name: 'App',
  data(){
  	return{
		Loading: false,//是否加载
  	}
  },
  methods:{
  	fullscreenLoading(isLoad){

            this.Loading = isLoad;

   
        }
  }
}
</script>

<style lang="less">
body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, form, fieldset, legend, input, textarea, button, p, blockquote, th, td
{margin: 0;padding: 0;}
ul,ol,li{ list-style: none;}
a{text-decoration: none;}
img{border: none;}
#app{
	width: 100%;
    height:100%;
    position: relative;
}
//头部样式  开始
header{
	width:100%;
	height:90/50rem;
	line-height: 90/50rem;
	background-color: #2B5FD8;
	font-size: 34/50rem;
	text-align: center;
	color: #fff;
	position: fixed;
	z-index: 999;
	top: 0;
	left:0;
}
header>i{
	position:absolute;
	top:3/50rem;
}
header>i.icon-xiangzuo1{
	left: 20/50rem;
}
header>i.icon-saoma{
	right: 25/50rem;
}
header>i::before{
	font-size:40/50rem;
}
header>i.icon-saoma::before{
	font-size:50/50rem;
}

@media screen and (max-width: 320px){

	header{
		font-size: 39/50rem;
		&>i::before{
			font-size:50/50rem;
		}
		&>i.icon-saoma::before{
			font-size:58/50rem;
		}
	}
}

@media screen and (max-width: 245px){

	header{
		font-size: 48/50rem;
		&>i::before{
			font-size:55/50rem;
		}
		&>i.icon-saoma::before{
			font-size:65/50rem;
		}
	}
}
//头部样式  结束

.tableContainer{
	span{
	 	text-overflow:ellipsis;
	 	white-space:nowrap;
	 	overflow:hidden;
	}
}

.container{
	margin-top: 90/50rem;
	font-size: 30/50rem;
}
@media screen and (max-width: 330px){
	.container{
		font-size: 36/50rem;
	}
}
@media screen and (max-width: 250px){
	.container{
		font-size: 48/50rem;
	}
}
//公共按钮等 开始
.g_tips{
	position: fixed;
	width:100%;
	z-index: 9999;
	height:100%;
	background-color: rgba(0,0,0,0.3);
	font-size: 32/50rem;
	color:#fff;
	top:0;
	left: 0;
	text-align:center;
	&>div{
		position:fixed;
		width:500/50rem;
		border-radius: 10/50rem;
		height:300/50rem;
		background-color: #fff;
		top:50%;
		left:125/50rem;
		margin-top: -150/50rem;
		color: #5D5D5E;
		&>span{
			display: block;
			font-weight: 700;
			border-radius:5/50rem;
		}
		&>span:nth-child(1){
			margin:20/50rem auto;

		}
		&>span:nth-child(2){
			font-weight: 300;
		}
		p{
			width: 100%;
			height: 70/50rem;
			line-height: 70/50rem;
			color: #285DCE;
			border-top: 3/50rem solid #285DCE;
			position: absolute;
			bottom: 0;
			left: 0;
		}
	}
	&>div.serverErr{
		&>span:nth-child(2){
			padding-top: 30/50rem;
			height: 100/50rem;
		}
		&>div{
			height: 60/50rem;
			line-height: 60/50rem;
			display: flex;
			span{
				flex-grow:1;
				background-color: #6897FF;
				color: #fff;
				margin-left: 30/50rem;
			}
			span:nth-child(2){
				margin-right: 30/50rem;
			}
		}
	}
}
@media screen and (max-width: 330px){

	.g_tips{
		font-size: 40/50rem;
		&>div{
			height:400/50rem;
			margin-top: -200/50rem;
		}
		&>div.serverErr{
			&>span:nth-child(2){
				height: 150/50rem;
				padding-top: 30/50rem;
			}
			&>div{
				height: 60/50rem;
				line-height: 60/50rem;
				display: flex;
				span{
					flex-grow:1;
					background-color: #6897FF;
					color: #fff;
					margin-left: 30/50rem;
				}
				span:nth-child(2){
					margin-right: 30/50rem;
				}
			}
		}
	}
}
@media screen and (max-width: 250px){

	.g_tips{
		font-size: 45/50rem;
		&>div{
			height:450/50rem;
			margin-top: -225/50rem;
			p{
				height: 90/50rem;
				line-height: 90/50rem;
			}
		}
	}
}
.g_singleBtn{
	width:50%;
	margin:10/50rem auto;
	display: block;
	text-align: center;
	background-color: #285DCE;
	height:70/50rem;
	color: #fff;
	line-height: 70/50rem;
	border-radius: 5/50rem;
}
.g_doubleBtn{
	width:100%;
	height:70/50rem;
	padding:0 20/50rem;
	margin-top: 80/50rem;
	box-sizing: border-box;
	&>div{	
		width:320/50rem;
		height:70/50rem;
		line-height: 70/50rem;
		color: #fff;
		background-color: #285DCE;
		text-align: center;
		float: left;
		border-radius: 5/50rem;
	}
	&>div:nth-child(1){
		margin-right: 70/50rem;
	}
}
.g_switchTab{
	margin:10/50rem auto;
	position: relative;
	text-align: center;
	&>div{
		position:absolute;
		width:100/50rem;
		top:0;
		i{
			font-size: 29/50rem;
		}
	}
	&>.previous{
		left: 10/50rem;
	}
	&>.next{
		right:10/50rem;
	}
}
p.positionFixed{
	width: 100%;
	height:130/50rem;
	position: fixed;
	bottom: 0;
}
//公共按钮等 结束

//错误提示
p.warningInfo{
	color: red;
	margin:10/50rem 20/50rem;
}
div.kong{
	height:200/50rem;
	border-bottom: none;
}

//选中icon图标样式
.icon-xuanzhong{
	color:#285DCE;
}

//input框样式
input{
	border-radius: 5/50rem;
	border:1/50rem solid #bbb;
	outline:none;
	text-transform:uppercase;
}
input:focus{ outline:#7BD039 solid 3/50rem; }


//选择订单和查看明细的表格公共样式
table{
	tr{
		td{
			min-width: 110/50rem;
			padding: 0 20/50rem;
			text-align: center;
			height: 85/50rem;
			border-left: 1/50rem solid #000;
			border-bottom: 1/50rem solid #000;
			.iconfont{
				font-size: 40/50rem;
			}
		}
		td:nth-child(1){
			border-left: none;
		}
	}
	thead{
		td{
			line-height: 85/50rem;
			i{
				display: none;
				float: right;
			}
		}
	}
}

@media screen and (max-width: 332px) and (min-width: 251px){
	table{
		tr{
			td{
				min-width: 130/50rem;
				.iconfont{
					font-size: 55/50rem;
				}
			}
		}
	}
}
@media screen and (max-width: 250px){
	table{
		tr{
			td{
				height: 105/50rem;
				min-width: 160/50rem;
				.iconfont{
					font-size:  60/50rem;
				}
			}
		}
	}
}

// 374像素以下（包含374像素）
@media screen and (max-width: 374px){

	input{
		border:3/50rem solid #bbb;
	}

	table{
		tr{
			td{
				border-left: 3/50rem solid #000;
				border-bottom: 3/50rem solid #000;
			}
		}
	}
}
</style>



