//http请求
Vue.prototype.httpRequest=function (url,params,success,focusInput){

	var that = this;

	that.$emit('isLoading', true);

	this.$axios.post(store.state.Server_Address + url,
		{
	      "params": JSON.stringify(params),
	      "WarehouseCode": that.$store.state.WarehouseCode,
	      "UserID":that.$store.state.UserID
	    },
	    {
	    	headers: {
	            'Content-Type': 'application/x-www-form-urlencoded',
	            "AuthCode":"cyscm",
	            "AuthID":''
	        }
	    }
	)
	.then(function(res){
		console.log(res)

		that.$emit('isLoading', false);

		if(res.data.status==1){

			success(res);

		}else{
			that.tipsShow(focusInput);

			if(focusInput){

				that.inputFocus = focusInput;
			}

			if(res.data.Msg){

				that.msg = res.data.Msg;

			}else{

				that.msg = "查询失败";
			}
		}
	})
	.catch(function(res){

		that.$emit('isLoading', false);

		that.tipsShow(focusInput);

		if(focusInput){
				
			that.inputFocus = focusInput;
		}

		if(url.split("/")[url.split("/").length-1] == "RFLogin.do"){
				
			that.server_err = true;

		}else{
				
			that.msg = "请求失败";
		}
	})
}

