// 冒泡排序
Vue.prototype.BubbleSort=function (arr,FieldName,SortCondition){

    var len = arr.length;

    for (var i = 0; i < len; i++) {

        for (var j = 0; j < len - 1 - i; j++) {

        	if(SortCondition){

        		if (arr[j][FieldName] < arr[j+1][FieldName]) {        //相邻元素两两对比

	                var temp = arr[j+1];        //元素交换
	                arr[j+1] = arr[j];
	                arr[j] = temp;
	            }
        	}else{

        		if (arr[j][FieldName] > arr[j+1][FieldName]) {        //相邻元素两两对比

	                var temp = arr[j+1];        //元素交换
	                arr[j+1] = arr[j];
	                arr[j] = temp;
	            }
        	}

            
        }
    }
    return arr;
	
}

// 点击表格头部 进行排序（按照表头字段排序）
Vue.prototype.SortArr=function(TableData,FieldName,SortCondition,e){

	var that = this;

	var eventElement = e.currentTarget;

	$(eventElement).children("i").css("display","inline-block");
	$(eventElement).siblings().children("i").css("display","none");

	var arr = this.BubbleSort(that[TableData],FieldName,this[SortCondition]);

	if(this[SortCondition]){

		this[SortCondition] = false;

		$(eventElement).children("i").removeClass('icon-down-trangle-copy-copy').addClass("icon-down-trangle");


	}else{
		
		this[SortCondition] = true;

		$(eventElement).children("i").removeClass('icon-down-trangle').addClass("icon-down-trangle-copy-copy");
	}
 
    // 先增加  再删除  顺序不可变
    arr.unshift(1);
    arr.shift();

    this[TableData] =  arr;
   return false;

    // 通过this.$set 设置为响应式数据，数据改变后，视图就会变化
    // this.$set(that[TableData], that.BubbleSort(that[TableData],FieldName))
}


