//设置cookie
Vue.prototype.setCookie_Server = function (c_server) {

    var exdate=new Date();//获取时间

    // 服务器地址永久保存
    var exdateForever=new Date();//获取时间
    exdateForever.setTime(exdateForever.getTime() + 24*60*60*1000*365*10);//保存的天数

    window.document.cookie="Server_Address"+"="+c_server+";path=/;expires="+exdateForever.toGMTString();
}

//读取服务器地址cookie
Vue.prototype.getCookie_Server = function () {

    if (document.cookie.length>0) {

      var arr=document.cookie.split('; ');//这里显示的格式需要切割一下自己可输出看下

      for(var i=0;i<arr.length;i++){

        var arr2=arr[i].split('=');//再次切割

        //判断查找相对应的值
        if(arr2[0]=='Server_Address'){

            this.$store.state.Server_Address = arr2[1];//保存到保存数据的地方

            console.log(arr2[1])
        }
      }
    }
}
//-----------------前端记住服务器地址代码 结束------------------------------------------------

//-----------------前端记住账号密码租户代码 开始------------------------------------------------

//设置cookie
Vue.prototype.setCookie = function(c_name,c_pwd,c_code,exdays) {

    var exdate=new Date();//获取时间

    exdate.setTime(exdate.getTime() + 24*60*60*1000*exdays);//保存的天数

    // 租户代码永久保存
    var exdateForever=new Date();//获取时间
    exdateForever.setTime(exdateForever.getTime() + 24*60*60*1000*365*10);//保存的天数

    //字符串拼接cookie
    window.document.cookie="userName"+ "=" +c_name+";path=/;expires="+exdate.toGMTString();
    window.document.cookie="userPwd"+"="+c_pwd+";path=/;expires="+exdate.toGMTString();
    window.document.cookie="Tenant_Code"+"="+c_code+";path=/;expires="+exdateForever.toGMTString();
}

//读取cookie
Vue.prototype.getCookie = function (e) {

    if (document.cookie.length>0) {

      var arr=document.cookie.split('; ');//这里显示的格式需要切割一下自己可输出看下

      for(var i=0;i<arr.length;i++){

        var arr2=arr[i].split('=');//再次切割

        //判断查找相对应的值
        if(arr2[0]=='userName'){

          this.userName=arr2[1];//保存到保存数据的地方

        }else if(arr2[0]=='userPwd'){

          this.password=arr2[1];
        }else if(arr2[0]=='Tenant_Code'){

          this.Tenant_Code=arr2[1];
        }
      }
    }
}

//清除cookie
Vue.prototype.clearCookie = function () {

    this.setCookie("","","",-1);//修改3值都为空，天数为负1天就好了
}

