import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    // 登录
    {
      path: '/',// 登录
      name: 'Login',
      
      // 路由懒加载
      component: resolve => require(['@/pages/Login/login'], resolve)
    },
    // 帐号与第三方关联
    {
      path:'/Binding',// 微信openid关联到帐号
      name: 'Binding',
      component: resolve => require(['@/pages/Login/Binding'], resolve)
    },
    {
      path: '/Index',//首页
      name: 'Index',
     component: resolve => require(['@/pages/index'], resolve)
    },
    // 库存查询
    {
      path: '/InventoryQuery', //库存查询
      name: 'InventoryQuery',
      component: resolve => require(['@/pages/inventoryQuery/InventoryQuery'], resolve)
    },
    {
      path:'/InventoryDetails',//库存详情
      name: 'InventoryDetails',
      component: resolve => require(['@/pages/inventoryQuery/InventoryDetails'], resolve)
    },
    {
      path:'/InventoryList',//库存列表
      name: 'InventoryList',
      component: resolve => require(['@/pages/inventoryQuery/InventoryList'], resolve)
    },
    // 上架
    {
      path: '/Putaway',//上架
      name: 'Putaway',
      component: resolve => require(['@/pages/putaway/putaway'], resolve)
    },
    {
      path:'/PutawayDetails',//上架 查看明细
      name: 'PutawayDetails',
      component: resolve => require(['@/pages/putaway/PutawayDetails'], resolve)
    },
    // 收货
    {
      path:'/Receipt',//收货首页（录入单号）
      name: 'Receipt',
      component: resolve => require(['@/pages/Receipt/Receipt'], resolve)
    },
    {
      path:'/ScanCommodityCode',//扫描商品条码
      name: 'ScanCommodityCode',
      component: resolve => require(['@/pages/Receipt/ScanCommodityCode'], resolve)
    },
    {
      path:'/BindingCommodityCode',//绑定商品条码
      name: 'BindingCommodityCode',
      component: resolve => require(['@/pages/Receipt/BindingCommodityCode'], resolve)
    },
    {
      path:'/TakeDelivery',// 收货
      name: 'TakeDelivery',
      component: resolve => require(['@/pages/Receipt/TakeDelivery'], resolve)
    },
    {
      path:'/Summary',//收货汇总
      name: 'Summary',
      component: resolve => require(['@/pages/Receipt/Summary'], resolve)
    },
    {
      path:'/SelectOrder',//选择订单
      name: 'SelectOrder',
      component: resolve => require(['@/pages/Receipt/SelectOrder'], resolve)
    },
    {
      path:'/CommodityInfoEntry',//商品信息录入
      name: 'CommodityInfoEntry',
      component: resolve => require(['@/pages/Receipt/CommodityInfoEntry'], resolve)
    },
    // 盘点
    {
      path:'/CycleCount', // 盘点
      name: 'CycleCount',
      component: resolve => require(['@/pages/CycleCount/CycleCount'], resolve)
    },
    {
      path:'/CycleCount_ChoiceNo',// 选择盘点单
      name: 'CycleCount_ChoiceNo',
      component: resolve => require(['@/pages/CycleCount/CycleCount_ChoiceNo'], resolve)
    },
    {
      path:'/CycleCount_Submit', // 盘点
      name: 'CycleCount_Submit',
      component: resolve => require(['@/pages/CycleCount/CycleCount_Submit'], resolve)
    },
    //拣货
    {
      path:'/RFPick',//拣货首页
      name: 'RFPick',
      component: resolve => require(['@/pages/RFPick/RFPick'], resolve)
    },
    {
      path:"/RFPick_select",//拣货 选择任务
      name: 'RFPick_select',
      component: resolve => require(['@/pages/RFPick/RFPick_select'], resolve)
    },
    {
      path:"/RFPick_confirm",//拣货 拣货确认页面
      name: 'RFPick_confirm',
      component: resolve => require(['@/pages/RFPick/RFPick_confirm'], resolve)
    },
    //库存移动
    {
      path:'/RFInvMove',//库存移动首页
      name: 'RFInvMove',
      component: resolve => require(['@/pages/RFInvMove/RFInvMove'], resolve)
    },
    // 发货
    {
      path:'/ConsignmentIndex',//发货序列号采集 首页
      name: 'ConsignmentIndex',
      component: resolve => require(['@/pages/ConsignmentNumCollect/ConsignmentIndex'], resolve)
    },
    {
      path:'/ConsignmentNumCollect',//发货序列号采集
      name: 'ConsignmentNumCollect',
      component: resolve => require(['@/pages/ConsignmentNumCollect/ConsignmentNumCollect'], resolve)
    },
    {
      path:'/CheckDetails', //发货序列号采集  查看明细
      name: 'CheckDetails',
      component: resolve => require(['@/pages/ConsignmentNumCollect/checkDetails'], resolve)
    },
    {
      path:'/ChoiceGoods',//发货序列号采集  选择商品
      name: 'ChoiceGoods',
      component: resolve => require(['@/pages/ConsignmentNumCollect/ChoiceGoods'], resolve)
    },
    // 发运
    {
      path:'/RFOrderShip',//发运首页
      name: 'RFOrderShip',
      component: resolve => require(['@/pages/RFOrderShip/RFOrderShip'], resolve)
    },
    {
      path:'/RFOrderDetailQuery', // 查看发运明细
      name: 'RFOrderDetailQuery',
      component: resolve => require(['@/pages/RFOrderShip/RFOrderDetailQuery'], resolve)
    },
    // 补货
    {
      path:'/ReplenishIndex',//补货首页
      name: 'ReplenishIndex',
      component: resolve => require(['@/pages/Replenishment/ReplenishIndex'], resolve)
    },
    {
      path:'/ReplenishArea',//补货-区域/库位
      name: 'ReplenishArea',
      component: resolve => require(['@/pages/Replenishment/ReplenishArea'], resolve)
    },
    {
      path:'/ReplenishTaskList',//补货-任务列表
      name: 'ReplenishTaskList',
      component: resolve => require(['@/pages/Replenishment/ReplenishTaskList'], resolve)
    },
    {
      path:'/Replenishment',//补货
      name: 'Replenishment',
      component: resolve => require(['@/pages/Replenishment/Replenishment'], resolve)
    },
    // 绑定商品条码
    {
      path:'/SkuBound',//绑定商品条码
      name: 'SkuBound',
      component: resolve => require(['@/pages/SkuBound/SkuBound'], resolve)
    },
    {
      path:'/StoreKey_select',//绑定商品条码-选择客户
      name: 'StoreKey_select',
      component: resolve => require(['@/pages/SkuBound/StoreKey_select'], resolve)
    },
    {
      path:'/Sku_select',//绑定商品条码-选择商品
      name: 'Sku_select',
      component: resolve => require(['@/pages/SkuBound/Sku_select'], resolve)
    },
    // 图表(报表)页面
    {
      path:'/Chart', // 图表
      name: 'Chart',
      component: resolve => require(['@/pages/chart/chart'], resolve)
    },
    //按单复核
    {
      path:'/CheckOrder',//按单复核-首页
      name:'CheckOrder',
      component: resolve => require(['@/pages/check/CheckOrder'], resolve)
    },
    {
   	  path:'/CheckOrder_check',//按单复核-复核
   	  name:'CheckOrder_check',
      component: resolve => require(['@/pages/check/CheckOrder_check'], resolve)
    },
    {
   	  path:'/CheckOrder_detail',//按单复核-查看明细
   	  name:'CheckOrder_detail',
      component: resolve => require(['@/pages/check/CheckOrder_detail'], resolve)
    },
    //按装运复核
    {
      path:'/CheckLoad',//按装运复核-首页
      name:'CheckLoad',
      component: resolve => require(['@/pages/check/CheckLoad'], resolve)
    },
    {
 	    path:'/CheckLoad_check',//按装运复核-复核
 	    name:'CheckLoad_check',
      component: resolve => require(['@/pages/check/CheckLoad_check'], resolve)
    },
    {
      path:'/CheckLoad_detail',//按装运复核-查看明细
      name:'CheckLoad_detail',
      component: resolve => require(['@/pages/check/CheckLoad_detail'], resolve)
    },
    // 系统信息
    {
      path:'/SystemInfo',// 系统信息
      name:'SystemInfo',
      component: resolve => require(['@/pages/SystemInfo/SystemInfo'], resolve)
    },
  ]
})



